<template>
    <div>
        <component :is="componentInstance"/>
    </div>
</template>

<script>
export default {
   name:"vueContainer",
   props:{
       url:{
           type:String,
           default:{}
       },
       component:{
           type:Object,
           default: null
       }
   },
   data(){
       return{
           componentInstance:this.component
       } 
   },
   watch:{
       url:function(old,last){
           this.setComponent();
       }
   },
   methods:{
       setComponent(){
         let _this =this;
         if(this.url.trim() !=""){
           window.blog.import(this.url).then(function(instance){
           _this.componentInstance = instance.default;
          });
        }
     }
   },
   created(){
       this.setComponent();
   }
}
</script>

