<template>
    <el-menu :default-active="activeIndex" mode="horizontal" @select="handleSelect">
        <template v-show="data!=null" v-for="menu in data">
            <el-menu-item v-if="menu.children == undefined " :index="menu.order" :key="menu.indentity" >{{ menu.title }}</el-menu-item>
            <el-submenu v-else  :index="menu.order" :key="menu.indentity">
                 <template slot="title">{{ menu.title }}</template>
                <el-menu-item v-for="submenu in menu.children" 
                    :index="submenu.order" 
                    :key="submenu.indentity">{{ submenu.title }}</el-menu-item>
            </el-submenu>
        </template>
    </el-menu>
</template>

<script>
import { getTimeStamp } from '../../utils/StringUtils.js';
export default {
   name:'blog-nav',
   data(){
       return {
           activeIndex: "0" ,
           data:null
       }
   },

   created(){
       this.getdata();
   },
   methods:{
       handleSelect(key,path){
          let urlObj=null;
          this.data.filter(item=>{
              if(item.order === key){
                  urlObj={
                      order: item.order,
                      title: item.title,
                      indentity:item.indentity,
                      type:item.type
                  }
                  return true;
              }else{
                  if(item.children != undefined){
                     item.children.filter(subItem =>{
                         if(subItem.order === key){
                             urlObj={
                                    order: subItem.order,
                                    title: subItem.title,
                                    indentity:subItem.indentity,
                                    type:subItem.type
                            };
                         }
                     })
                  }
              }
          });
          if(urlObj.type =="text"){
              
              window.location.hash='#'+urlObj.indentity;
              this.$router.push(urlObj.indentity);
          }
       },
       getdata(){
           let _this=this;
           let timestamp = getTimeStamp();
           this.$http.get('db/navMenu/menu.json?version='+timestamp).then(res=>{
               _this.data=res.data;
               _this.getdefaultIndex().bind(_this);
           }).catch(err=>{
               console.log(" get menu info error !");
           });
       },
       getdefaultIndex(){
           var hash =window.location.hash.replace('#','');
           var order="";
           this.data.filter(item=>{
              if(item.indentity === hash){
                  order= item.order;
                  return true;
              }else{
                  if(item.children != undefined){
                     item.children.filter(subItem =>{
                         if(subItem.indentity === hash){
                            order = subItem.order;
                            return true;
                         }
                     })
                  }
              }
          });
          this.activeIndex=order;
       }
   }
}
</script>

<style>
    .el-menu{
        background-color: #40caffe6;
        color:#fff;
    }
    .el-menu.el-menu--horizontal {
        border-bottom: solid 1px #40caffe6;
    }
    .el-menu--horizontal>.el-menu-item:not(.is-disabled):focus, .el-menu--horizontal>.el-menu-item:not(.is-disabled):hover, .el-menu--horizontal>.el-submenu .el-submenu__title:hover {
    background-color:  #40caffe6;
    }
    .el-menu--horizontal>.el-submenu:focus .el-submenu__title, .el-menu--horizontal>.el-submenu:hover .el-submenu__title {
      color: #fff;
    }
  
    .el-menu--horizontal>.el-menu-item.is-active {
    border-bottom: 2px solid #409EFF;
    color: #fff;
    }
    .el-menu--horizontal>.el-submenu.is-active .el-submenu__title {
    border-bottom: 2px solid #409EFF;
    color: #fff;
}
</style>
