import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

/**
 * 公共状态保持
 */
const store = new Vuex.Store({
    state: {
      count: 0,
      pageObj:{
          indentity:'',
          category:'',
          label:''
      }
    },
    mutations: {
      increment (state) {
        state.count++
      },
      changePageObj(state,obj){
          state.pageObj = obj;
      }
    }
  });

export default store;