<template>
  <div v-loading="loading">
    <el-row class="navbar">
        <el-breadcrumb separator-class="el-icon-arrow-right">
          <el-breadcrumb-item v-for="(nav,index) in navArr" :key="index">
              <i v-if="index ===0 " class="el-icon-s-tools"/> {{nav}}
          </el-breadcrumb-item>
        </el-breadcrumb>
    </el-row>
    <grid-layout v-if="layout.length>0"
      :layout.sync="layout"
      :col-num="12"
      :row-height="30"
      :is-draggable="false"
      :is-resizable="false"
      :is-mirrored="false"
      :vertical-compact="true"
      :margin="[10, 15]"
      :use-css-transforms="true"
      :style="{'height':height+'px'}"
    >
      <grid-item v-for="item in layout"
        :x="item.x"
        :y="item.y"
        :w="item.w"
        :h="item.h"
        :i="item.i"
        :key="item.url.replace('/','')"
        :class="item.s"
      ><blog-container  :url="item.url" ></blog-container>
      </grid-item>
    </grid-layout>
  </div>
</template>

<script>
import VueGridLayout from "vue-grid-layout";
import { getTimeStamp } from '../../utils/StringUtils.js';
import blogContainer from './container.vue';
export default {
  components: {
    GridLayout :VueGridLayout.GridLayout,
    GridItem :VueGridLayout.GridItem,
    blogContainer
  },
  created(){
    this.getData();
    this.getlayout();
    this.height=window.document.documentElement.clientHeight-120;
    console.log(this.height);
  },
  data() {
    return {
      loading:true,
      navArr:[],
      data:null,
      layout:[]
    };
  },
  watch:{
    $route:function(old,newVal){
      this.getlayout();
      this.getData();
    }
  },
  methods:{
      getData(){
        var hash = window.location.hash.replace('#','').trim();
        let _this=this;
        let timestamp =getTimeStamp();
        this.navArr.splice(0,this.navArr.length);
         this.$http.get('db/navMenu/menu.json?v='+timestamp).then(res=>{
               _this.data=res.data;
               _this.data.filter(item=>{
                  if(item.indentity === hash){
                     _this.navArr.push(item.title);
                     return true;
                  }else{
                     if(item.children!=undefined){
                        item.children.filter(subItem=>{
                           if(subItem.indentity === hash){
                             _this.navArr.push(item.title);
                             _this.navArr.push(subItem.title);
                           }
                        })
                     }
                  }
               })
           });
      },
      getNavArr(){
           var hash = window.location.hash.replace('#','').trim();
           this.navArr.splice(0,this.navArr.length);
           if(this.data !=null){
               this.data.filter(item=>{
                  if(item.indentity === hash){
                     this.navArr.push(item.title);
                     return true;
                  }else{
                     if(item.children!=undefined){
                        item.children.filter(subItem=>{
                           if(subItem.indentity === hash){
                             this.navArr.push(item.title);
                             this.navArr.push(subItem.title);
                           }
                        })
                     }
                  }
               });
           }else{
             this.getData();
          }
          
      },
      getlayout(){
        let _this=this;
        let hash = window.location.hash.replace('#','').trim();
        let date = new Date();
        let version = getTimeStamp();
        this.layout.splice(0,this.layout.length);
        this.$http.get("db/layout/gridlayout.json?v="+version).then(res=>{
    
            res.data.filter(item=>{
              if(item.indentity === hash){
                _this.layout =item.children;
              }
            });
            _this.loading=false;
        }).catch(err=>{
          console.log(err);
        });
      }
  }
};
</script>

