 .navbar{
    height: 50px;
    background-color: #F2F6FC;
    line-height: 50px;
    padding-top: 19px;
    font-size: 16px;
    padding-left:10px;
 }
 .y{
   background-color: yellow;
 }
 .b{
   background-color: blue;
 }
 .g{
   background-color: green;
 }
 .r{
   background-color: red;
 }
