import Vue from 'vue'
import VueRouter from "vue-router"
import layoutContainer from '../component/layout/blogGridLayout.vue'
import index from '../pages/window/index.vue';
import pages from './pagelist.js';
Vue.use(VueRouter);

const routes = [
    { path:'/',component:index},
    { path:'/index',component:index}
];

pages.forEach(item=>{
    routes.push({
        path:item,
        component:layoutContainer
    });
});

const router = new VueRouter({
    routes // （缩写）相当于 routes: routes
});


router.beforeEach((to, from, next) => {

    let urlhash=window.location.hash.replace('#','').trim();
    // ...
    /**
     * 继续往下走
     */
    if(urlhash === to.path){
        next();
    }else{
        if(urlhash !=''){
            next(urlhash);
        }else{
            next(new Error('not found page !'));
        }
    }
    
});

export default router;